html {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0 0 20px;
}

button {
  margin: 0;
  padding: 0;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 40px;
}

.header {
  background-color: #00008b;
  margin-bottom: 30px;
  padding: 20px 0;
}

.search-item {
  justify-content: center;
  gap: 5px;
  display: flex;
}

.button-search {
  height: 25px;
  width: 25px;
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 25%;
}

.search-icon {
  fill: #00008b;
  margin: auto;
}

.gallery {
  flex-wrap: wrap;
  gap: 20px;
  margin-bottom: 40px;
  display: flex;
}

.photo-card {
  width: calc(25% - 15px);
  border-radius: 25px;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .2);
}

.photo-card > img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 25px;
}

.info {
  justify-content: center;
  align-items: center;
  gap: 10px;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  gap: 5px;
  display: flex;
}

.load-more {
  text-align: center;
  color: #fff;
  cursor: pointer;
  background-color: #00008b;
  border: none;
  border-radius: 4px;
  margin: 0 auto;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold;
  text-decoration: none;
  display: block;
}

.load-more:hover, .load-more:active {
  background-color: #45a049;
}

.load-more.is-hidden {
  display: none;
}

